import btc from "@/assets/btc.png";
import chuangkouguanbi from "@/assets/chuangkouguanbi.png";
import confirmPayment from "../component/confirmPayment";
import payment1 from "@/assets/payment.png";
import payment2 from "@/assets/payment2.png";
export default {
  data() {
    return {
      showPositionValue: false, //提示信息
      toastContent: "",
      title: "选择付款方式",
      butText: "下一步",
      btc: btc,
      payment2: payment2,
      payment1: payment1,
      close: chuangkouguanbi,
      checkIcon: false,
      isShow: false,
      isConfirmPayment: false,
      activeIndex: -1,
      pay_type: "",
      order_id: 0,
      isShowComponents: true,
      // goodsId:"",
      // buyNum:"",
      // payType:"",
      list: []
    };
  },
  components: {
    confirmPayment
  },
  props: {
    isBuyingPatterns: { type: Boolean },
    goods_id: Number,
    buy_num: Number
    // pay_type:String
    // "支付类型 1支付宝 2微信 3BTC 4USDT 5CNC",
  },
  created() {
    this.getPayment();
  },
  mounted() {},
  methods: {
    getPayment() {
      var that = this,
        formData = {
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token
        };
      that
        .$request({
          method: "POST",
          url:"/order/payment",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          that.list = response.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    //单选按钮TODO
    getPaycheck(i, id) {
      this.activeIndex = i;
      this.pay_type = id;
    },
    closeFun() {
      this.$emit("closeFun");
    },
    isCloseFun() {
      this.isConfirmPayment = false;
      this.$router.go(0);
    },
    submit() {
      var that = this,
        formData = {
          goods_id: that.goods_id,
          buy_num: that.buy_num,
          pay_type: that.pay_type,
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token
        };
      if (this.pay_type != "") {
        that
          .$request({
            method: "POST",
            url:"/order/create-order",
            data: that.$qs.stringify(formData)
          })
          .then(function(response) {
            that.order_id = response.data.order_id;
            if (
              that.pay_type == 1 ||
              that.pay_type == 2 ||
              that.pay_type == 7
            ) {
              that.isConfirmPayment = true;
              that.isShowComponents = false;
            } else {
              that.$router.push({
                name: "affirmPay",
                params: {
                  id: response.data.order_id
                }
              });
            }
          })
          .catch(function(error) {
            console.log(error);
          });
      } else {
        that.showPositionValue = true;
        that.toastContent = "请选择支付方式";
      }
    }
  }
};



