
import Range from 'vux/src/components/range/index.vue'


import buyingPatterns from "../my/component/buyingPatterns";


import loading from "@/assets/loading.gif";
import storage from "../../../common/storage.js";
export default {
data() {
return {


loading: loading,
isShow: true,
showPositionValue: false, //提示信息
toastContent: "",
topTitle: "购买",
rangedata: 10,
rangedataVal: 0,
agreed: false,
isBuyingPatterns: false,
isdisabled: true,
agreed: false,
placeholderStart_num: "",
form: {
goods_id:0,
stock: 0,
start_num: 10,
price: "0",
contract_cycle: "0",
exchange: "0"
},
newVal: "",
newValIndex: 0,
newValData: "",
newValIndexType: 0
};
},
filters: {
uppercase: function(value) {
if (!value) {
return "";
}
return value.toUpperCase();
}
},
components: {
Range,

buyingPatterns
},
created() {
if (!this.$route.params.id) {
this.$nextTick(function() {
if (storage.get("order-detail")) {
this.form = storage.get("order-detail");
this.isShow = false;
}
});
} else {
this.getDetails();
}
},
computed: {},
methods: {

getRange() {
var that = this;
that.newValIndex++;
if (that.newValIndex >= 2) {
that.newValIndex = 0;
} else if (Number(that.rangedataVal) != 0) {
console.log("newValIndex:" + that.newValIndex);
if (that.newValIndexType == 1) {
that.newValIndexType = 0;
} else {
that.rangedata = parseInt(
that.form.stock * (that.rangedataVal / 100)
);
}






}
},
getInp(val) {
var that = this;
that.newValData = val;
that.newValIndex++;
if (that.form.stock < 1) {
that.rangedataVal = 0;
} else {
if (that.newValIndex >= 2) {
if (
that.newValIndex == 1 &&
that.newValData.length != 0 &&
that.newValData.length < val.length
) {
console.log(
"newValData.length:" +
that.newValData.length +
"val.length:" +
val.length
);
that.newValIndex = 0;
that.rangedataVal = (val / that.form.stock) * 100;
} else {
that.newValIndex = 0;
}
} else {
that.rangedataVal = (val / that.form.stock) * 100; 
that.newVal = that.rangedataVal;
that.newValIndexType = 1;
}
}













},

getCheckIcon() {







if (this.agreed == true) {
this.isdisabled = false;
this.agreed == true;
} else {
this.isdisabled = true;
this.agreed == false;
}
},

getDetails() {
var that = this,
formData = {
sign: that.$store.state.user.sign,
access_token: that.$store.state.user.token,
goods_id: that.$route.params.id 
};
that
.$request({
method: "POST",
url:"/goods/goods-detail",
data: that.$qs.stringify(formData)
})
.then(function(response) {
console.log(response);
if (response.code == 0) {
that.placeholderStart_num = response.data.start_num + "份起购";
that.rangedata = response.data.start_num;
that.form = response.data;
that.isShow = false;
storage.set("order-detail", that.form);
}
})
.catch(function(error) {
console.log(error);
});
},
closeFun() {
this.isBuyingPatterns = false;
},
submit() {
if (this.rangedata < this.form.start_num) {
this.showPositionValue = true;
this.toastContent =
"购买数量最少" + this.form.start_num + "份，请重新操作";
} else if (Number(this.rangedata) > Number(this.form.stock)) {
this.showPositionValue = true;
this.toastContent = "购买数量超过库存，请重新操作";
} else {
if (this.isBuyingPatterns == false) {
this.isBuyingPatterns = true;
} else {
this.isBuyingPatterns = false;
}
}
},
getXY() {
this.$router.push("/xieYi");
}
}
};



