// import btc from "@/assets/btc.png";
import chuangkouguanbi from "@/assets/chuangkouguanbi.png";
export default {
  data() {
    return {
      showPositionValue: false, //提示信息
      toastContent: "",
      title: "确认付款",
      butText: "确认支付",
      // btc: btc,
      close: chuangkouguanbi,
      checkIcon: false,
      list: {
        order_id: "",
        pay_total: "",
        pay_coin_type: "",
        cell1: "订单金额",
        cell2: "订单信息",
        cell3: "付款方式"
      }
    };
  },
  props: {
    isConfirmPayment: { type: Boolean },
    order_id: Number
  },
  updated() {
    this.getData();
  },
  methods: {
    getPay() {
      var that = this,
        formData = {
          order_id: that.order_id,
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token
        };
      that
        .$request({
          method: "POST",
          url:"/order/confirm-order",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          if (response.code == 0) {
            that.showPositionValue = true;
            that.toastContent = response.errmsg;
            setTimeout(function() {
              that.$router.push({
                name: "myOrder",
                params: {
                  index:"one"
                }
              });
            }, 500);

            // that.$router.push({
            //   name: "updateSuccess",
            //   params: {
            //     title: "您已购买完成！",
            //     page: "我的订单",
            //     RouterUrl: "/myOrder"
            //   }
            // });
          } else {
            that.showPositionValue = true;
            that.toastContent = response.errmsg;
            // setTimeout(function() {
            //   that.$router.push("/productDetails");
            // }, 2000);
          }
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    getData() {
      var that = this,
        formData = {
          order_id: that.order_id,
          sign: that.$store.state.user.sign,
          access_token: that.$store.state.user.token
        };
      that
        .$request({
          method: "POST",
          url:"/order/order-detail",
          data: that.$qs.stringify(formData)
        })
        .then(function(response) {
          that.list.pay_coin_type = response.data.pay_coin_type.toUpperCase();
          that.list.pay_total = response.data.pay_total;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    isCloseFun() {
      this.$emit("isCloseFun");
    }
  }
};



